import React from 'react';
import Jumbotron from 'react-bootstrap/Jumbotron'
import Container from 'react-bootstrap/Container'
import Carousel from 'react-bootstrap/Carousel'
import "./App.css"

function Jumbo(){

    return(
        <a href="#">
            <Carousel>
                <Carousel.Item>
                    <Jumbotron fluid className="jumbotron">
                        <img src="" alt="" />
                        <Container>
                            <h1>HOT CUBE</h1>
                            <p>The first no crew fast food restaurant in Indonesia.</p>
                        </Container>
                    </Jumbotron>
                </Carousel.Item>
                <Carousel.Item>
                    <Jumbotron fluid className="jumbotron">
                        <img src="" alt="" />
                        <Container>
                            <h1>DISKON 50%</h1>
                            <p>Fried Chicken</p>
                        </Container>
                    </Jumbotron>
                </Carousel.Item>
                <Carousel.Item>
                    <Jumbotron fluid className="jumbotron">
                        <img src="" alt="" />
                        <Container>
                            <h1>SUPER DUPER PROMO OF THE MONTH</h1>
                            <p>Only 10k for a chicken pok pok</p>
                        </Container>
                    </Jumbotron>
                </Carousel.Item>
            </Carousel>
            
        </a>
    )
}

export default Jumbo