import React from 'react';
import Container from 'react-bootstrap/Container'

function Cart() {
    return (
        <div>
            <Container className="margin">
                <h1>Order Summary</h1>
                <ul>
                    <li>Fried Chicken - 3x</li>
                    <p>Rp 60,000</p>
                    <li>Fried Noodle - 3x</li>
                    <p>Rp 30,000</p>
                    <li>Lemon Tea - 3x</li>
                    <p>Rp 40,000</p>
                    <p>Total</p>
                    <p>Rp 130,000</p>
                </ul>

                <h2>Payment Method</h2>
                <p>Go Pay</p>
            </Container>
        </div>
        
    )
}

export default  Cart;